 body,
 html {
     width: 100%;
     height: 100%;
 }
 
 body,
 h1,
 h3,
 h4,
 h5,
 h6 {
     font-family: "Lato","Helvetica Neue",Helvetica,Arial,sans-serif;
     font-weight: 700;
     color: grey;
 }
 
 .navbar-default .navbar-nav > li > a:hover, .navbar-default .navbar-nav > li > a:focus {
       color: #3498db;
 }
 
 
 .navbar-brand {
    float: left;
    height: 50px;
    padding: 0;
    font-size: 18px;
    line-height: 20px;
}
 
 
 h2{
   font-family: "Lato","Helvetica Neue",Helvetica,Arial,sans-serif;
   font-weight: 700;
   color: #3498db;
 
 }
 
 .topnav .navbar-default {
     font-size: 14px;
     padding-top: 2px;
     padding-bottom: 2px;
 }
 
 .lead {
     font-size: 18px;
     font-weight: 400;
 }
 
 .intro-header {
     padding-top: 50px; /* If you're making other pages, make sure there is 50px of padding to make sure the navbar doesn't overlap content! */
     padding-bottom: 50px;
     text-align: center;
     color: #f8f8f8;
     background-size: cover;
 }
 
 .intro-message {
     position: relative;
 }
 
 .intro-message > h1 {
     margin: 0;
     color: #3498db;
     font-size: 5em;
 }
 
 .intro-divider {
     width: 400px;
     border-top: 1px solid #f8f8f8;
     border-bottom: 1px solid rgba(0,0,0,0.2);
 }
 
 .intro-message > h3 {
   color: grey;
 }
 
.big-youtube-icon{
    font-size: 75px;
}

.big-github-icon{
    font-size: 75px;
}

.project-items-heading{
    color: grey;
}

.projects-in-progress{
    color: rgb(216, 202, 0);
}

.projects-done{
    color: green;
}

.projects-cancelled{
    color: red;
}

.big-terminal{
    font-size:25px;
}

.big-download{
    font-size:50px;
}

.list-decimal{
    list-style: decimal;
}

.span-top-25-px{
    padding-top: 25px;
}

.loading-icon{
    color: #6cbff7;
}

.download-list{
    padding-bottom: 20px;
}

.site-icon-theme{
    color: #3498db;
}

.scroll-wrapper {
	-webkit-overflow-scrolling: touch;
  	overflow-y: scroll;
}

.margin-bottom-5-perc{
    margin-bottom: 5%;
}

 @media(max-width:767px) {
     .intro-message {
         padding-bottom: 15%;
     }
 
     .intro-message > h1 {
         font-size: 3em;
     }
 
     ul.intro-social-buttons > li {
         display: block;
         margin-bottom: 20px;
         padding: 0;
     }
 
     ul.intro-social-buttons > li:last-child {
         margin-bottom: 0;
     }
 
     .intro-divider {
         width: 100%;
     }
 }
 
 .network-name {
     text-transform: uppercase;
     font-size: 14px;
     font-weight: 400;
     letter-spacing: 2px;
 }
 
 .content-section-a {
     padding: 50px 0;
     background-color: #f8f8f8;
 }
 
 .content-section-b {
     padding: 50px 0;
     border-top: 1px solid #e7e7e7;
     border-bottom: 1px solid #e7e7e7;
 }
 
 .section-heading {
     margin-bottom: 30px;
 }
 
 .section-heading-spacer {
     float: left;
     width: 200px;
     border-top: 3px solid #e7e7e7;
 }
 
 .banner {
     padding: 100px 0;
     color: #f8f8f8;
     /*background: url(../img/banner-bg.jpg) no-repeat center center;*/
     background-size: cover;
 }
 
 .banner h2 {
     margin: 0;
     text-shadow: 2px 2px 3px rgba(0,0,0,0.6);
     font-size: 3em;
 }
 
 .banner ul {
     margin-bottom: 0;
 }
 
 .banner-social-buttons {
     float: right;
     margin-top: 0;
 }
 
 @media(max-width:1199px) {
     ul.banner-social-buttons {
         float: left;
         margin-top: 15px;
     }
 }
 
 @media(max-width:767px) {
     .banner h2 {
         margin: 0;
         text-shadow: 2px 2px 3px rgba(0,0,0,0.6);
         font-size: 3em;
     }
 
     ul.banner-social-buttons > li {
         display: block;
         margin-bottom: 20px;
         padding: 0;
     }
 
     ul.banner-social-buttons > li:last-child {
         margin-bottom: 0;
     }
 }
 
 footer {
     padding: 50px 0;
     border-top: 1px solid #e7e7e7;
     border-bottom: 1px solid #e7e7e7;
 }
 
 p.copyright {
     margin: 15px 0 0;
 }
